// === VARIABLES ===

$primary-color: #ff63e8;
$secondary-color: #ffa6d7;

@mixin boxShadow {
  -webkit-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}

// === HEADER === //

// === PREVIEW === //

.preview {
  position: relative;
  .school-header {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;

    .nav-link {
      color: #ffff;
    }
    .active {
    }
    .navbar-nav {
    }
  }
  &-block {
    position: relative;
    background-image: url("/img/school-prewiev-pro.webp");
    background-size: no-repeat;
    background-position: 35% 65%;
    background-size: cover;
    width: 100%;
    height: 650px;
    background-attachment: scroll;
    filter: blur(1.5px);
    @media (max-width: 900px) {
      background-position: 25% 75%;
    }
    @media (max-width: 650px) {
      background-position: 15% 85%;
    }
    @media (max-width: 320px) {
      // height: 600px;
    }
    &__title {
      color: #fff;
      position: absolute;
      top: 100px;
      left: 30px;
      max-width: 650px;
      @media (max-width: 320px) {
        left: 20px;
        top: 175px;
        height: 450px;
        h1 {
          font-size: 20px;
        }
      }
      & span {
        color: $secondary-color;
      }
      & p {
        font-size: 14px;
        width: 40%;

        @media (max-width: 650px) {
          width: 70%;
        }
      }
      & button {
        color: #fff;
      }
    }
  }
}
// === PREVIEW === //

.astro-links {
  &__subtitle {
    font-size: 16px;
    width: 100%;
    padding: 50px 80px;
    border: 1px solid $secondary-color;
    border-radius: 10px;
    @include boxShadow();
    text-align: center;
  }
}
.btn-school {
  // box-shadow: none;
  background-color: #fff;
  border: 1px solid $secondary-color;
  padding: 6px 80px;
  text-transform: capitalize;
  background-color: $secondary-color;
  color: #fff;
  font-weight: bold;
  @include boxShadow();
  a {
    color: #fff;
  }
  &:hover {
    border: 1px solid $primary-color;
    background-color: $primary-color;
  }
}
// === CONDITION === //

.condition {
  .tilesWrap {
    padding: 0;
    margin: 50px auto;
    list-style: none;
    text-align: center;

    li {
      display: inline-block;
      width: 75%;
      min-width: 200px;
      max-width: 320px;
      padding: 80px 20px 40px;
      position: relative;
      vertical-align: top;
      margin: 10px;
      font-family: "helvetica", san-serif;
      min-height: 45vh;
      background: #ffffff;
      border: 1px solid #252727;
      text-align: left;

      p {
        text-align: center;
        font-size: 24px;
        line-height: 26px;
        color: #000000;
        margin: 0 auto;
        width: 85%;
      }

      &:before {
        content: "";
        position: absolute;
        top: -2px;
        left: -2px;
        right: -2px;
        bottom: -2px;
        z-index: -1;
        background: #fff;
        transform: skew(2deg, 2deg);
      }

      &:after {
        content: "";
        position: absolute;
        width: 40%;
        height: 100%;
        left: 0;
        top: 0;
        background: rgba(255, 255, 255, 0.02);
      }

      &:nth-child(1):before {
        background: #c9ffbf;
        background: -webkit-linear-gradient(to right, #ffafbd, #c9ffbf);
        background: linear-gradient(to right, #ffafbd, #c9ffbf);
      }

      &:nth-child(2):before {
        background: #f2709c;
        background: -webkit-linear-gradient(to right, #ff9472, #f2709c);
        background: linear-gradient(to right, #ff9472, #f2709c);
      }

      &:nth-child(3):before {
        background: #c21500;
        background: -webkit-linear-gradient(to right, #ffc500, #c21500);
        background: linear-gradient(to right, #ffc500, #c21500);
      }

      &:nth-child(4):before {
        background: #fc354c;
        background: -webkit-linear-gradient(to right, #0abfbc, #fc354c);
        background: linear-gradient(to right, #0abfbc, #fc354c);
      }
    }
  }
}

// === RATES === //

.card {
  @include boxShadow();
}

.btn-credit {
  max-width: 220px;
  display: inline-block;
  font-size: 14px;
  background: #f2709c;
  color: #fff;

  padding: 10px;
  border-radius: 10px;
  @include boxShadow();
  cursor: pointer;
  -webkit-transition: all ease-in-out 300ms;
  transition: all ease-in-out 300ms;
  a {
    color: #fff;
    font-family: inherit;
  }
}
.btn-credit:hover {
  background: $secondary-color;
}

// === EXPERT === //

.expert {
  &-block {
    &__photo {
      width: 70%;
    }
  }
  &-info {
    &--list {
      &__item {
        padding: 20px;
        margin-bottom: 15px;
        border-radius: 30px;
        background-image: linear-gradient(
          315deg,
          $primary-color 0%,
          $secondary-color 74%
        );
        box-shadow: 5px 5px 20px rgb(203 205 211 / 10%);

        --border-size: 3px;
        --border-angle: 0turn;
        background-image: conic-gradient(
            from var(--border-angle),
            #ffffff,
            rgb(255, 255, 255) 50%,
            #ffffff
          ),
          conic-gradient(
            from var(--border-angle),
            transparent 20%,
            rgb(0, 255, 242),
            rgb(255, 93, 225)
          );
        background-size: calc(100% - (var(--border-size) * 2))
            calc(100% - (var(--border-size) * 2)),
          cover;
        background-position: center center;
        background-repeat: no-repeat;

        animation: bg-spin 3s linear infinite;
        @keyframes bg-spin {
          to {
            --border-angle: 1turn;
          }
        }
        &:hover {
          animation-play-state: paused;
        }
      }
    }
  }
}

.pink-text {
  color: #ff63e8;
}

